import {connect} from "react-redux";
import {login} from "../../Redux/auth-reducer";
import {Redirect} from "react-router-dom";
import {Field, Form} from "react-final-form";
import React from "react";
import {requiredField} from "../../Utils/Validators/validator";
import s from "./Login.module.css"
import {FORM_ERROR} from "final-form";

const composeValidators = (...validators) => value =>
    validators.reduce((error, validator) => error || validator(value), undefined)

const LoginForm = (props) => {
    return (
        <Form
            onSubmit={async values => {
                let x = await props.login(values.email, values.password, values.rememberMe);
                if (x) {
                    return {[FORM_ERROR]: x}
                }
            }}
            render={(props) => {
                return (
                    <form onSubmit={props.handleSubmit}>
                        <div>
                            <Field name="email" validate={requiredField}>
                                {({input, meta}) => {
                                    const hasError = meta.touched && meta.error;
                                    return (
                                        <div>
                                            <input {...input} type="text" placeholder="login" className={hasError && s.error}/>
                                        </div>
                                    )
                                }}
                            </Field>

                            <Field name="password" validate={requiredField}>
                                {({input, meta}) => {
                                    const hasError = meta.touched && meta.error;
                                    return (
                                        <div>
                                            <input {...input} type="password" placeholder="password" className={hasError && s.error}/>
                                        </div>
                                    )
                                }}
                            </Field>

                            <Field name="rememberMe">
                                {({input, meta}) => {
                                    return (
                                        <div>
                                            <input {...input} type="checkbox"/>
                                            Remember me
                                        </div>
                                    )
                                }}
                            </Field>
                            {props.submitError && <div className="error">{props.submitError}</div>}
                        </div>
                        <button disabled={props.submitting || props.pristine}>Login</button>
                    </form>
                )
            }}
        />
    )
}


const Login = ({isAuth, login}) => {
    if (isAuth) {
        return (
            <Redirect to={"/profile"}/>
        )
    }

    return (
        <div className='form'>
            <h1>
                Login
            </h1>
            <LoginForm login={login}/>
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login})(Login);